<script>
export default {
  name: 'index',
  data() {
    return {
      // 通知相关
      showToast: false,
      toastMessage: '',
      toastType: 'success'
    }
  },
  methods: {
    goBack() {
      this.$goBackOrHome()
    },
    navigateTo(index) {
      if(index == 0) {
        if(localStorage.getItem('aoding')) {
          this.showNotification('请先对配置进行设置', 'error');
        } else {
          this.$router.push('/aodingScript');
        }
      } else if(index == 1) {
        this.$router.push('/configuration');
      } else if(index == 2) {
        this.$router.push({ path: '/webrtc', query: { name: '奥丁同步工具', id: 0 } });
      } else {
        this.$router.push('/result');
      }
    },
    showNotification(message, type = 'success') {
      this.toastMessage = message;
      this.toastType = type;
      this.showToast = true;
      
      setTimeout(() => {
        this.showToast = false;
      }, 3000);
    },
  }
}
</script>

